<template>
<v-content>
    <v-container fluid fill-height>
        <v-layout align-center justify-center>
            <v-flex xs12 sm8>
                <v-card class="elevation-12">
                    <v-toolbar dark color="teal">
                        <v-toolbar-title>Cadastre-se</v-toolbar-title>
                    </v-toolbar>
                    <v-card-text>
                        <v-form ref="form" v-model="valid" lazy-validation>
                            <v-text-field v-model="user.name" :rules="nameRules" label="Nome"></v-text-field>
                            <v-text-field v-model="user.cnpj" return-masked-value mask="##.###.###/####-##" :rules="nameRules" label="CNPJ"></v-text-field>
                            <v-subheader class="pa-0 mt-3">Informações de contato</v-subheader>
                            <v-text-field v-model="user.contato.telefone_fixo" :rules="nameRules" label="Número do telefone fixo"></v-text-field>
                            <v-text-field v-model="user.email" :rules="emailRules" label="E-mail"></v-text-field>
                            <v-text-field v-model="user.password" type="password" label="Password"></v-text-field>
                            <v-text-field v-model="password2" type="password" label="Password"></v-text-field>
                            <v-alert :value="erroPassword" type="error">
                                Senhas incompativeis
                            </v-alert>
                            <v-btn @click="submit" color="teal" dark>
                                Cadastrar
                            </v-btn>
                            <v-btn @click="clear">Cancelar</v-btn>
                        </v-form>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</v-content>
</template>

<script>
import userService from "../services/userService";

export default {
    data: () => ({
        valid: true,
        name: '',
        menu: false,
        menu_rg: false,
        date: null,
        date1: null,
        user: {
            contato: {}
        },
        nameRules: [
            v => !!v || 'Requerido',
        ],
        erroPassword: false,
        email: '',
        emailRules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+/.test(v) || 'E-mail must be valid'
        ],
        password2: '',
        select: null,

        checkbox: false
    }),

    methods: {
        verificarPassword() {
            if (this.password2 === this.user.password) {
                return true
            }
            this.erroPassword = true
            return false
        },
        submit() {
            //console.log(this.user);
            if (this.verificarPassword()) {
                userService
                    .cadastroClient(this.user).then(r => {
                        if (r.status == 201) {
                            this.$router.push("/login");
                        }
                    });
            } else {

            }
        },
        clear() {
            this.$refs.form.reset()
            this.$router.push("/login");
        }
    }
}
</script>
